<template>
<div id="app">
  <Header />
  <AddTodo />
  <Todo v-bind:todos = "todos" v-on:del-todo = 'deleteTodo' />
</div>
</template>

<script>
import Header from './components/Header.vue'
import Todo from './components/Todo.vue'
import AddTodo from './components/AddTodo.vue'

export default {
  name: 'App',
  components: {
 Header,
 Todo,
 AddTodo
  },
  data() {
    return {
      todos: [
        {
      id:1,
      title: 'Todo One',
      completed:false
    },
    {
    id:2,
    title:'Todo Two',
    completed: true
  },
  {
    id:3,
    title:'Todo Three',
    completed: false
  }
      ]
}
  },
methods: {
  deleteTodo(id) {
    this.todos = this.todos.filter(todo => todo.id !== id)
  }
}
}
</script>

